angular.module('core.drivers',[]);
angular
  .module('core.drivers')
  .factory('ergastAPIservice', function($http){

    var ergastAPI = {};

    ergastAPI.getDrivers = function () {
      return $http({
        method: 'JSONP',
        url: 'http://ergast.com/api/f1/2016/driverStandings.json?callback=JSON_CALLBACK'
      });
    };

    ergastAPI.getDriverDetails = function(id){
      return $http({
        method: 'JSONP',
        url: 'http://ergast.com/api/f1/2016/drivers/'+ id +'/driverStandings.json?callback=JSON_CALLBACK'
      });
    };

    ergastAPI.getDriverRaces = function(id){
      return $http({
        method: 'JSONP',
        url: 'http://ergast.com/api/f1/2016/drivers/'+ id +'/results.json?callback=JSON_CALLBACK'
      });
    };

    return ergastAPI;

  });
angular.module('driverDetails',['core.drivers']);
angular.module('driverDetails')
  .component('driverDetails',{
    templateUrl:'app/driver-details/driver-details.template.html',
    controllerAs:'ctrl',
    controller: function driverDetailsController(ergastAPIservice, $routeParams){
      var self = this;
      self.id = $routeParams.id;

      ergastAPIservice.getDriverDetails(self.id).success(function (response){
        self.driver = response.MRData.StandingsTable.StandingsLists[0].DriverStandings[0];
      });

      ergastAPIservice.getDriverRaces(self.id).success(function(response){
        self.races = response.MRData.RaceTable.Races;
      });
    }
  });

'use strict';

var phoneListModule = angular.module('driversList', ['core.drivers']);


'use strict';

angular.module('driversList')
  .component('driversList',{
    templateUrl:'app/drivers-list/drivers-list.template.html',
    controllerAs:'ctrl',
    controller: function driverListController(ergastAPIservice){
      var self = this;

      ergastAPIservice.getDrivers().success(function(response){
        self.drivers = response.MRData.StandingsTable.StandingsLists[0].DriverStandings;

      });

      self.searchFiller = function(driver){
        var keyword = new RegExp(self.nameFilter,'i');
        return !self.nameFilter || keyword.test(driver.Driver.givenName) || keyword.test(driver.Driver.familyName);
      };

    }
  });


'use strict';

angular.module('app',[
  'driversList',
  'driverDetails',
  'ngRoute'
  ]);



angular.module("app").run(["$templateCache", function($templateCache) {$templateCache.put("app/driver-details/driver-details.template.html","<section>\n  \n  <nav id=\"secondary\" class=\"driver-nav small-shadow\">\n    <div class=\"driver-picture\">\n        <img class=\"driver-img\" ng-src=\"img/drivers/{{ctrl.driver.Driver.driverId}}.png\">\n        <br>\n        {{ctrl.driver.Driver.givenName}} {{ctrl.driver.Driver.familyName}}\n    </div>\n    <div class=\"driver-status\">\n      Country: {{ctrl.driver.Driver.nationality}}   <br>\n      Team: {{ctrl.driver.Constructors[0].name}}<br>\n      Birth: {{ctrl.driver.Driver.dateOfBirth}}<br>\n      <a href=\"{{ctrl.driver.Driver.url}}\" target=\"_blank\">Biography</a>\n      <br>\n      <a href=\"./#/drivers\">Back to drivers list</a>\n    </div>\n  </nav>\n\n  <div class=\"small-shadow\">\n    <table>\n      <thead>\n        <tr><th colspan=\"5\">Formula 1 2016 Results</th></tr>\n      </thead>\n      <tbody>\n        <tr>\n          <td>Round</td> <td>Grand Prix</td> <td>Team</td> <td>Grid</td> <td>Race</td>\n        </tr>\n        <tr ng-repeat=\"race in ctrl.races\">\n          <td>{{race.round}}</td>\n          <td><img ng-src=\"img/flags/{{race.Circuit.Location.country}}.png\">{{race.raceName}}</td>\n          <td>{{race.Results[0].Constructor.name}}</td>\n          <td>{{race.Results[0].grid}}</td>\n          <td>{{race.Results[0].position}}</td>\n        </tr>\n      </tbody>\n    </table>\n  </div>\n\n</section>");
$templateCache.put("app/drivers-list/drivers-list.template.html","<input class=\"small-shadow\" type=\"text\" ng-model=\"ctrl.nameFilter\" placeholder=\" search...\">\n<table class=\"small-shadow\">\n  <thead>\n    <tr>\n      <th colspan=\"4\"> Drivers Championship Standings</th>\n    </tr>\n  </thead>\n  <tbody>\n    <tr ng-repeat=\"driver in ctrl.drivers | filter: ctrl.searchFiller\">\n      <td>{{$index + 1}}</td>\n      <td>\n        <img ng-src=\"/img/flags/{{driver.Driver.nationality}}.png\">\n        <a href=\"#/drivers/{{driver.Driver.driverId}}\">{{driver.Driver.givenName}}\n        {{driver.Driver.familyName}}</a>\n      </td>\n      <td>{{driver.Constructors[0].name}}</td>\n      <td>{{driver.points}}</td>\n    </tr>\n  </tbody>\n</table>");}]);
angular.module('app')
  .config(['$locationProvider','$routeProvider', 
    function config($locationProvider, $routeProvider){
      
      $routeProvider.
      when('/drivers',{
        template:'<drivers-list></drivers-list>'
      }).
      when('/drivers/:id',{
        template:'<driver-details></driver-details>'
      }).
      otherwise('/drivers');
    }
  ]);